import { element } from 'protractor';
import { Component, ViewChild, ElementRef } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  host: { '(window:keydown)': 'hotkeys($event)' },
})
export class AppComponent {
  symbols: FormGroup;
  instruments: FormArray;
  @ViewChild('quantity') quantity: ElementRef;
  @ViewChild('price') price: ElementRef;
  @ViewChild('target') target: ElementRef;
  @ViewChild('stoploss') stoploss: ElementRef;
  @ViewChild('symbol') symbol: ElementRef;

  hotkeys(event) {
    console.log(event);
    switch (event.code) {

      case 'KeyB': {
        this.orderDetails.controls["transaction_type"].setValue("BUY");
        this.placeOrder();
      }
        break;
      case "KeyS":
        this.orderDetails.controls["transaction_type"].setValue("SELL");
        this.placeOrder();
        break;
      case "KeyY":
        this.symbol.nativeElement.focus();

        break;
      // case "Enter":
      //   this.placeOrder();
      // break;
      case "KeyQ": {
        if (event.altKey) {
          this.quantity.nativeElement.select();
          this.quantity.nativeElement.focus();
        }
        else {
          if (event.shiftKey) {
            this.quantity.nativeElement.stepDown();
          }
          else {
            this.quantity.nativeElement.stepUp();
          }
        }

        this.orderDetails.controls["quantity"].setValue(this.quantity.nativeElement.value);
      }

        break;

      case "KeyP": {
        if (event.altKey) {
          this.price.nativeElement.select();
          this.price.nativeElement.focus();
        }
        else {

          if (event.shiftKey) {
            this.price.nativeElement.stepDown();
          }
          else {
            this.price.nativeElement.stepUp();
          }
        }
        this.orderDetails.controls["price"].setValue(this.price.nativeElement.value);
      }

        break;
      case "KeyT": {
        if (event.shiftKey) {
          this.target.nativeElement.stepDown();
        }
        else {
          this.target.nativeElement.stepUp();
        }
        this.orderDetails.controls["squareoff_value"].setValue(this.target.nativeElement.value);

      }

        break;

      case "KeyL": {
        if (event.shiftKey) {
          this.stoploss.nativeElement.stepDown();
        }
        else {
          this.stoploss.nativeElement.stepUp();
        }
        this.orderDetails.controls["stoploss_value"].setValue(this.stoploss.nativeElement.value);
      }

        break;
    }


  }



  orderDetails: FormGroup;
  constructor(private http: HttpClient, private formBuilder: FormBuilder) {



    this.orderDetails = this.formBuilder.group({
      "tradingsymbol": ["JPASSOCIAT"],
      "quantity": [50],
      "price": [0],
      "squareoff_value": [0.1],
      "stoploss_value": [0.5],
      "transaction_type": ["SELL"]
    })

    this.instruments = this.formBuilder.array([
    ])
    this.symbols = this.formBuilder.group({
      instruments: this.instruments
    })

  }
  ngOnInit() {
    if (window.location.href.indexOf("request_token") != -1) {
      if(window.location.href.indexOf("appspot.com")!=-1){
        this.http.get(window.location.href.replace("appspot.com", "appspot.com/zr/setRequestToken")).subscribe((data) => {
          console.log(data);
          window.location.href = "https://zngu-192516.appspot.com";
        })
      }
      else{
        this.http.get(window.location.href.replace("8080", "8080/zr/setRequestToken")).subscribe((data) => {
          console.log(data);
          window.location.href = "http://localhost:8080";
        })
      }
    

    }

    this.http.get('zr/getInstruments').subscribe((data: any) => {
      data.forEach((element, index) => {
        this.instruments.push(this.getInstrumentGroup())
        let temp = (<FormArray>this.symbols.controls.instruments)
        console.log(temp);
        temp.controls[index].setValue(element);
      });
    })

  }

  updateInstruments(value){
    this.http.post("zr/updateInstrument",value).subscribe((data) => {
      console.log(data);
    })
  }

getInstrumentGroup(){
  return this.formBuilder.group({
    "tradingsymbol": [],
    "quantity": [40],

    "squareoff_value": [0.1],
    "stoploss_value": [0.5],
    token: [],
    "upperRSI": [70],
    "lowerRSI": [30],
    isEnabled: [1]
  })
}

  addInstrument(){
    this.instruments.push(this.getInstrumentGroup());
  }
  add(value){
    this.http.post("zr/addInstrument",value).subscribe((data) => {
      console.log(data);
    })
    
  }

  getPositions() {

    this.http.get("zr/getPosition").subscribe((data) => {
      console.log(data);
    })




  }

  getMargin() {
    this.http.get("zr/getMargin").subscribe((data) => {
      console.log(data);
    })


  }
  placeOrder() {
    console.log(this.orderDetails.value);

    this.http.post("zr/placeOrder", this.orderDetails.value).subscribe((data: any) => {
      console.log(data);

    })
  }
  clear() {


    this.http.get("zr/clear").subscribe((data: any) => {
      console.log(data);

    })
  }

  login() {

    this.http.get("zr/getLoginUrl").subscribe((data: any) => {
      window.location.href = data.url;

    })



  }


  connect() {
    this.http.get("zr/connect").subscribe((data: any) => {
      console.log(data);

    })

  }

  get formData() { 
    return (<FormArray>this.symbols.controls.instruments).controls;
  }

}



